<template>
  <a-config-provider :locale="locale">
    <div id="app">
      <a-layout style="min-height:100vh">
        <Header />

        <a-layout-content>
          <a-alert
            closable
            type="warning"
            message="注意：当前项目处于开发中，下一版发布会清空数据。非常感谢您的使用！"
            style="margin: 16px 24px;"
          />
          <router-view />
          <a-back-top />
        </a-layout-content>

        <a-layout-footer style="text-align: center">
          OOS v.{{ APP_VERSION }} ©2020 Created by
          <a :href="'https://' + DOMAIN" target="_blank">
            {{ DOMAIN }}
          </a>
        </a-layout-footer>
      </a-layout>
    </div>
  </a-config-provider>
</template>

<script>
import { mapState } from "vuex";
import _ from "lodash";
import moment from "moment";
import Header from "@/components/Header";
import { DOMAIN } from "@/dict";

export default {
  name: "app",
  components: {
    Header
  },
  data() {
    return {
      DOMAIN,
      APP_VERSION: process.env.VUE_APP_VERSION,
      locale: _.cloneDeep(window.antd.locales.zh_CN)
    };
  },
  computed: {
    ...mapState({
      localeCode: store => store.locale.selected.code,
      loginAuth: store => store.login.auth
    })
  },
  watch: {
    localeCode() {
      this.handleLocale();
    }
  },
  created() {
    this.handleLocale();
  },
  methods: {
    handleLocale() {
      if (this.localeCode) {
        this.locale = _.cloneDeep(
          window.antd.locales[this.localeCode] || window.antd.locales.zh_CN
        );

        moment.locale(this.locale.locale);
      }
    }
  },
  metaInfo() {
    return {
      htmlAttrs: {
        lang: this.locale.locale
      },
      titleTemplate: "%s - OOS"
    };
  }
};
</script>
