/**
 * Image转图片文件
 * @function
 * @param {Image} image    必需，Image图片
 * @param {Number} width   必需，图片宽度
 * @param {Number} height  必需，图片高度
 * @param {String} type    必需，图片格式
 * @param {Number} quality 压缩质量，值0~1，默认值1
 * @returns 返回Promise; then(blob=>{})
 */
export const imageToFile = ({ image, width, height, type, quality }) => new Promise(resolve => {
  const cvs = document.createElement('canvas')
  const ctx = cvs.getContext('2d')
  cvs.width = width
  cvs.height = height
  ctx.drawImage(image, 0, 0, width, height)
  cvs.toBlob(resolve, type, quality || 1)
})

/**
 * 图片url转Image
 * @function
 * @param {String} url 图片url
 * @returns 返回Promise；then(image=>{}).catch(err=>{})
 */
export const urlToImage = url => new Promise((resolve, reject) => {
  const img = new Image()
  img.addEventListener('load', () => {
    resolve(img)
  })
  img.addEventListener('error', reject)
  img.src = url
})

/**
 * 图片文件转图片url
 * @function
 * @param {File} file 文件
 * @returns 返回Promise；then(url=>{}).catch(err=>{})
 */
export const fileToUrl = file => new Promise((resolve, reject) => {
  const reader = new FileReader()
  reader.addEventListener('load', () => {
    resolve(reader.result)
  })
  reader.addEventListener('error', reject)
  reader.readAsDataURL(file)
})

/**
 * 压缩图片
 * @function
 * @param   {File}    file    图片文件
 * @param   {Number}  width   图片宽度
 * @param   {Number}  height  图片高度
 * @param   {Number}  quality 压缩质量，值0~1，默认值1
 * @returns 返回Promise；then(blob=>{})
 */
export const compressImage = ({ file, width, height, quality }) => new Promise(resolve => {
  // file -> Image -> blob
  fileToUrl(file).then(url => {
    urlToImage(url).then(image => {
      let type = file.type;
      if (/^image\//.test(type)) {
        type = "image/webp"
      }
      imageToFile({ image, width, height, type, quality }).then(resolve)
    })
  })
})
